@use 'base/variables' as v;
@use 'base/mixins' as m;


.video {
    position: relative;
    max-height: 70rem;
    min-height: 40rem;
    overflow: hidden;
    .overlay {
        position: absolute;
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        background: rgb(64,61,116);
        background: linear-gradient(90deg, rgba(64,61,116,0.8858893899356618) 0%, rgba(60,213,245,0.373284347918855) 100%);
    }
    video{
        width: 100%;
        display: block;
        @include m.mitablet {
            width: auto;
        }
       }
}

.video-contenido{
    z-index: 1;
    h2{
        color: v.$blanco;
        font-size: 7rem;
        margin: 0;
    }

    p{
        background-color: red;
        display: inline-block;
        color: v.$blanco;
        padding: 1rem 2rem;
        font-weight: bold;
        margin: 0;
    }
}