@use 'base/variables' as v;
@use 'base/mixins' as m;

.header {
    background-color: rgba(12, 3, 12, 0.336);
    position: fixed;
    width: 100%;
    z-index: 2;
    @include m.mitablet {
        background-color: rgb(0, 87, 128);
        position: relative;
    }
    h1 {
        color: v.$blanco;
        margin: 0;
    }
}

.contenedor-header{
    display: flex;
    justify-content: space-between;
    padding: 2rem;
    gap: 2rem;
    h1{
        text-align: center;
    }
    
    @include m.mitablet{
        flex-direction: column;   
    }
}
nav{
    display: flex;
    gap: 2rem;
    justify-content: center;
    align-items: center;
    a{
        color: v.$blanco;
        font-size: 2.4rem;
        text-align: center;
        margin-bottom: 0rem;
        opacity: .5;
        &:hover{
            opacity: 1;
        }
    }
    @include m.phone {
        flex-direction: column;
    }
}
